<script>

    export default {
        name: 'userVideo',
        created() {
            if(this.$isMobile()) {
                location.replace('/liveStream/userVideoMobile/'+this.$route.params.id)
            } else {
                location.replace('/liveStream/userVideoPc/'+this.$route.params.id)
            }
        }
    }
</script>
